import { Container, Row, Col } from "react-bootstrap"
import { ArrowRightCircle } from "react-bootstrap-icons"
import headerImg from "../assets/img/header-img.svg"
import { useEffect, useState } from "react"
import TrackVisibility from "react-on-screen"
import { isVisible } from "@testing-library/user-event/dist/utils"

export default function Banner() {

    // Ecriture automatique
    const [loopNum, setLoopNum] = useState(0)
    const [isDeleting, setIsDeleting] = useState(false)
    const toRotate = [ "Web Developer", "Web Design", "UI/UX Designer"]
    const [text, setText] = useState('')
    const [delta, setDelta] = useState(300 - Math.random() * 300)
    const period = 2000

    useEffect(() => {
        let ticker = setInterval(() => {
            tick()
        },delta)
        return () => {clearInterval(ticker)}
    }, [text])

    const tick = () => {
        let i = loopNum % toRotate.length;
        let fullText = toRotate[i];
        let upDateText = isDeleting ? fullText.substring(0, text.length - 1): fullText.substring(0, text.length + 1)

        setText(upDateText)

        if(isDeleting){
            setDelta(prevDelta => prevDelta /2)
        }

        if(!isDeleting && upDateText === fullText){
            setIsDeleting(true)
            setDelta(period)
        }else if(isDeleting && upDateText === ''){
            setIsDeleting(false)
            setLoopNum(loopNum + 1)
            setDelta(500)
        }

    }

    return(
        <section className="banner" id="home">
            <Container>
                <Row className="align-items-center">
                    <Col xs={12} lg={7}>
                      <TrackVisibility>
                      {({isVisible}) =>
                        <div className={isVisible ? "animate__animated animate__bounce" : ""}>
                        <span className="tagline">Welcome to my Porfolio</span>
                        <h1>{`Hi I'm webdecoded `}<span className="wrap">{text}</span></h1>
                        <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Hic repellat consequatur consectetur ipsum fugiat doloribus, debitis eaque esse, explicabo consequuntur qui, quas accusamus quam. Eum consequuntur eos nam incidunt voluptates?
                        </p>
                        <button onClick={() => console.log('connect')}>Let's connect <ArrowRightCircle size={25}/> </button>
                        </div>}
                      </TrackVisibility>
                    </Col>
                    <Col xs={12} lg={5}>
                        <img src={headerImg} alt="Header Img"/>
                    </Col>
                </Row>
            </Container>
        </section>
    )

}